import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/Index.vue'
import List from './views/List.vue'

Vue.use(Router)

export default new Router({
  //mode: 'history',
  //base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index
    },
    {
      path: '/list',
      name: 'list',
      component: List
    }
  ]
})
